import { Component, Prop, h } from '@stencil/core';
import { iFormField } from '../../models/form-field.model';


@Component({
  tag: 'form-legend',
  styleUrl: 'form-legend.css',
  shadow: false,
  scoped: false,
})
export class FormLegend {
  @Prop({ mutable: false }) field: iFormField;

  render() {
    return (
      <section class="legend-field-section">
        <div class="content">
          {this.field.label && <h3>{this.field.label}</h3>}
          {this.field.placeholder && <p>{this.field.placeholder}</p>}
        </div>
      </section>
    );
  }
}
